/* eslint-disable no-undef */
class DogShopApi {
  constructor({ baseUrl }) {
    this.baseUrl = baseUrl
    this.token = ''
  }

  getAuthorizationHandler() {
    return `Bearer ${this.token}`
  }

  setToken(token) {
    this.token = token
  }

  checkToken() {
    if (!this.token) throw new Error('    ')
  }

  //   
  async signin(values) {
    const res = await fetch(`${this.baseUrl}/signin`, {
      method: 'POST',
      headers: {
        'Content-type': 'application/json',
      },
      body: JSON.stringify(values),
    })
    if (res.status === 401) {
      throw new Error('   ')
    }

    if (res.status === 404) {
      throw new Error('   email  ')
    }

    if (res.status >= 300) {
      throw new Error(`,  ${res.status}`)
    }
    return res.json()
  }

  //   
  async signUp(values) {
    const res = await fetch(`${this.baseUrl}/signup`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(values),
    })
    if (res.status === 400) {
      throw new Error('    ')
    }

    if (res.status === 409) {
      throw new Error('   email  ')
    }
    if (res.status >= 300) {
      throw new Error(`,  ${res.status}`)
    }
    return res.json()
  }

  //    
  async getShowAllProducts(search, token) {
    const res = await fetch(`${this.baseUrl}/products/search?query=${search}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    })
    if (res.status >= 400 && res.status < 500) {
      throw new Error(`      . 
        . Status: ${res.status}`)
    }

    if (res.status >= 500) {
      throw new Error(`      . 
         . Status: ${res.status}`)
    }
    return res.json()
  }

  //       
  async getProductsByIds(ids, token) {
    return Promise.all(
      ids.map((id) => fetch(`${this.baseUrl}/products/${id}`, {
        headers: {
          authorization: `Bearer ${token}`,
        },
      }).then((res) => res.json())),
    )
  }

  //    id 
  async getDetailsProduct(id, token) {
    const res = await fetch(`${this.baseUrl}/products/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    })
    return res.json()
  }

  //      
  async getUserInfo(token) {
    const res = await fetch(`${this.baseUrl}/v2/sm9/users/me`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    })
    return res.json()
  }

  //   
  async addReviews(productId, token, values) {
    const res = await fetch(`${this.baseUrl}/products/review/${productId}`, {
      method: 'POST',
      headers: {
        authorization: `Bearer ${token}`,
        'Content-type': 'application/json',
      },
      body: JSON.stringify(values),
    })
    return res.json()
  }

  //   
  async addNewProduct(token, values) {
    const res = await fetch(`${this.baseUrl}/products`, {
      method: 'POST',
      headers: {
        authorization: `Bearer ${token}`,
        'Content-type': 'application/json',
      },
      body: JSON.stringify(values),
    })
    return res.json()
  }

  //   
  async deleteMyProduct(productId, token) {
    const res = await fetch(`${this.baseUrl}/products/${productId}`, {
      method: 'DELETE',
      headers: {
        authorization: `Bearer ${token}`,
        'Content-type': 'application/json',
      },
    })
    if (res.status === 404) {
      throw new Error(`. Status: ${res.status}`)
    }
    return res.json()
  }

  //   
  async edditAddMyProduct(productId, token, values) {
    const res = await fetch(`${this.baseUrl}/products/${productId}`, {
      method: 'PATCH',
      headers: {
        authorization: `Bearer ${token}`,
        'Content-type': 'application/json',
      },
      body: JSON.stringify(values),
    })
    return res.json()
  }

  //    
  async getReviewsById(token, productId) {
    const res = await fetch(`${this.baseUrl}/products/review/${productId}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    })
    return res.json()
  }
}

export const dogShopApi = new DogShopApi({
  baseUrl: 'https://api.react-learning.ru',
})
