/* eslint-disable linebreak-style */

import * as Yup from 'yup'

export const createFormSignUpValidator = Yup.object({
  email: Yup.string()
    .email('  ')
    .required(' email'),
  group: Yup.string()
    .min(3, ' 3 ')
    .max(20, ' 20 ')
    .required('  '),
  password: Yup.string()
    .min(5, ' 5 ')
    .max(25, ' 20 ')
    .required(' '),
})
